(function () {

/////////////////////////////////////////////////////////////////////////////////////////////////////
//                                                                                                 //
// packages/xinranxiao:accounts-spotify/spotify_common.js                                          //
//                                                                                                 //
/////////////////////////////////////////////////////////////////////////////////////////////////////
                                                                                                   //
Accounts.oauth.registerService('spotify');                                                         // 1
/////////////////////////////////////////////////////////////////////////////////////////////////////

}).call(this);






(function () {

/////////////////////////////////////////////////////////////////////////////////////////////////////
//                                                                                                 //
// packages/xinranxiao:accounts-spotify/spotify_server.js                                          //
//                                                                                                 //
/////////////////////////////////////////////////////////////////////////////////////////////////////
                                                                                                   //
Accounts.addAutopublishFields({                                                                    // 1
  forLoggedInUser: _.map(                                                                          // 2
    // publish access token since it can be used from the client                                   // 3
    // refresh token probably shouldn't be sent down.                                              // 4
    Spotify.whitelistedFields.concat(['accessToken', 'expiresAt']), // don't publish refresh token // 5
    function (subfield) { return 'services.spotify.' + subfield; }),                               // 6
                                                                                                   // 7
  forOtherUsers: _.map(                                                                            // 8
    // even with autopublish, no legitimate web app should be                                      // 9
    // publishing all users' emails                                                                // 10
    _.without(Spotify.whitelistedFields, 'email', 'id', 'uri'),                                    // 11
    function (subfield) { return 'services.spotify.' + subfield; })                                // 12
});                                                                                                // 13
/////////////////////////////////////////////////////////////////////////////////////////////////////

}).call(this);
